
"use client";

import LanguageContext from "@/app/context/LanguageContext";
import { useContext } from "react";
import staticTranslations from "../translations/static";

interface StaticTextProps {
  translationKey: keyof typeof staticTranslations;
}

const StaticText: React.FC<StaticTextProps> = ({ translationKey }) => {
  const { language } = useContext(LanguageContext);
  

  if (!staticTranslations[translationKey]) {
    console.warn(`Translation key "${translationKey}" not found`);
    return <>{translationKey}</>;
  }
  
  return <>{staticTranslations[translationKey][language]}</>;
};

export default StaticText;