"use client"

import { useContext, useEffect, useState } from "react";
import StaticText from "../translation/StaticText";
import { Loader2 } from "lucide-react";
import staticTranslations from "../translations/static";
import LanguageContext from "@/app/context/LanguageContext";

export interface LoadingProps {
  type: 'predictions' | 'chart' | 'profile' | 'auth';
  isAuth?: boolean;
}

const LoadingComponent: React.FC<LoadingProps> = ({ type, isAuth = false }) => {
  const { language } = useContext(LanguageContext);
  const [mysticalEffect, setMysticalEffect] = useState(false);
  const [stars, setStars] = useState<Array<{ top: number; left: number; duration: number; delay: number }>>([]);
  const [isMounted, setIsMounted] = useState(false);

  // Generate stars only on client side after mount
  useEffect(() => {
    setStars(
      Array.from({ length: 10 }).map(() => ({
        top: Math.random() * 100,
        left: Math.random() * 100,
        duration: 2 + Math.random() * 2,
        delay: Math.random() * 2,
      }))
    );
    setIsMounted(true);
  }, []);

  useEffect(() => {
    const timer = setInterval(() => {
      setMysticalEffect((prev) => !prev);
    }, 2000);
    return () => clearInterval(timer);
  }, []);

  const messageKeys: Record<LoadingProps['type'], keyof typeof staticTranslations> = {
    predictions: 'loadingPredictions',
    chart: 'calculatingChart',
    profile: 'loadingProfile',
    auth: 'authenticating',
  };

  const messageKey = messageKeys[type];

  return (
    <div className="min-h-screen bg-linear-to-br from-indigo-50 to-purple-100 flex items-center justify-center relative overflow-hidden">
      <div className="absolute inset-0 opacity-10 pointer-events-none">
        <div className="absolute top-1/4 left-1/4 w-32 h-32 bg-purple-300 rounded-full filter blur-3xl animate-pulse" />
        <div className="absolute bottom-1/4 right-1/4 w-40 h-40 bg-indigo-300 rounded-full filter blur-3xl animate-pulse" />
        {isMounted && stars.map((star, i) => (
          <div
            key={i}
            className="absolute w-1 h-1 bg-white rounded-full"
            style={{
              top: `${star.top}%`,
              left: `${star.left}%`,
              animation: `twinkle ${star.duration}s infinite`,
              animationDelay: `${star.delay}s`,
            }}
          />
        ))}
      </div>

      <div className="text-center z-10">
        <Loader2
          className={`mx-auto mb-6 animate-spin transition-all duration-500 ${
            isAuth ? 'w-12 h-12 text-blue-600' : 'w-16 h-16 text-purple-600'
          } ${mysticalEffect ? 'drop-shadow-[0_0_10px_rgba(168,85,247,0.7)]' : ''}`}
        />
        <p
          className={`font-medium transition-colors duration-300 ${
            isAuth ? 'text-lg text-gray-600' : 'text-xl text-gray-700'
          } ${mysticalEffect ? 'text-purple-700' : ''}`}
        >
          <StaticText translationKey={messageKey} />
        </p>
        <p className="text-gray-500 mt-2">
          <StaticText translationKey="pleaseWait" />
        </p>
      </div>

      <style>{`
        @keyframes twinkle {
          0%, 100% { opacity: 0; }
          50% { opacity: 1; }
        }
      `}</style>
    </div>
  );
};

export default LoadingComponent;